.contacts {
  display: block;
  width: 480px;
  text-align: center;
  margin: auto;
  padding: 0;
}

.stringElem {
  margin-right: 10px;
}

.list {
  list-style: none;
  display: block;
  padding: 1px;
  margin: 0;
  /* outline: 1px solid tomato; */
}

.item {
  box-sizing: border-box;
  margin-top: 4px;
  margin-bottom: 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 12px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  /* outline: 1px solid tomato; */
}

/* .item + .item {
} */

.button {
  width: 32px;
  height: 32px;
  padding: 0;
  margin: 0;
  cursor: pointer;
  border: none;
  border-radius: 4px;
}

.icon{
  background-color: red;
  border-radius: 4px;
  margin-bottom: -2px;
  fill:#fff;
  height: 30px;
  width: 30px;
}

.enter{
  transform: translateX(-100%);
}
.enterActive{
  transform: translateX(0);
  transition: all 250ms linear;
}

.exit{
  transform: translateX(0);
}

.exitActive{
  transform: translateX(-100%);
  transition: all 250ms linear;
}